.section-problem {
    display: flex;
    margin-left: auto;
    margin-right: auto;
    padding-top: 0;
    padding-bottom: 0;

}

.problem-baner {

    position: relative;



    background-image: linear-gradient(to bottom, rgba(0, 0, 0, 1), rgba(255, 255, 255, 0.9));
    min-height: 545px;

    @media screen and (min-width: $tablet) {
        min-height: 340px;
    }

    @media screen and (min-width: $desktop) {
        min-height: 560px;
        background-image: url(/images/noun_campfir.svg), linear-gradient(to bottom, rgba(17, 21, 24, 1), rgba(241, 242, 245, 0.4));
        ;
        background-repeat: no-repeat;
        background-position-x: right;
        background-position-y: bottom;
    }

}


.problem-title {
    display: inline-block;
    width: 280px;
    padding-top: 24px;
    font-weight: 800;
    font-size: 24px;
    line-height: 1.3;
    letter-spacing: 0.03em;
    color: var(--third-text-color);


    @media screen and (min-width: $tablet) {
        padding-top: 40px;
        width: 428px;
        font-weight: 800;
        font-size: 24px;
        line-height: 1.3;
        letter-spacing: 0.03em;
        color: var(--third-text-color);
    }

    @media screen and (min-width: $desktop) {
        padding-top: 80px;
        width: 866px;
        font-weight: 800;
        font-size: 46px;
        line-height: 1.3;
        letter-spacing: 0.03em;
        color: var(--third-text-color);
    }
}

.problem-title__accent {
    color: var(--accent-color);
}

.problem-container-list {
    


    padding: 20px 12px 24px 32px;
    top: 203px;
    width: 280px;


    @media screen and (min-width: $tablet) {
        top: 169px;
        width: 704px;
        padding: 32px 80px;

    }

    @media screen and (min-width: $desktop) {
        top: 338px;
        width: 1648px;
        padding: 60px 192px 60px 140px;


    }

    box-shadow: -5px 8px 26px 0px rgba(0, 0, 0, 0.04);
    background-color: var(--third-text-color);
    counter-reset: number 0;
}

.problem-list {
    display: flex;
    flex-wrap: wrap;

    @media screen and (min-width: $tablet) {
        margin-left: -20px;
        margin-top: -24px;
    }

    @media screen and (min-width: $desktop) {
        margin-left: -154px;
        margin-top: -40px;
    }


}

.problem-list__item:not(:last-child) {
    @media screen and (max-width: 767px) {
        margin-bottom: 16px;
    }
}

.problem-list__item {

    font-weight: 700;
    font-size: 14px;
    line-height: 1.4;
    letter-spacing: 0.08em;
    color: var(--second-text-color);


    @media screen and (min-width: $tablet) {

        margin-left: 20px;
        margin-top: 24px;

        flex-basis: calc(100%/2 - 24px);

    }

    @media screen and (min-width: $desktop) {

        margin-left: 154px;
        margin-top: 40px;
        flex-basis: calc(100%/3 - 154px);
        font-weight: 700;
        font-size: 20px;
        line-height: 1.2;
    }


    &::before {
        content: "— " counter(number);
        counter-increment: number;
        display: block;
        margin-bottom: 5px;
        font-weight: 700;
        font-size: 20px;
        line-height: 1.2;
        letter-spacing: 0.08em;
        color: var(--accent-color);

        @media screen and (min-width: $tablet) {
            margin-bottom: 4px;
            font-size: 20px;
            line-height: 1.2;
            color: var(--accent-color);
        }

        @media screen and (min-width: $desktop) {
            margin-bottom: 8px;
            font-size: 24px;
            line-height: 1.4;
            color: var(--accent-color);
        }
    }
}



